import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'	
export default {
	data() {
		return {
			addressList:[],
			hasFlag:true,
			page:0,
			flag:0,//为1是代表从结算页面进入
			isEmpty:false
		};
	},
	methods:{
		handleEditAddress(id,item){
			uni.navigateTo({
				url:'editAddress?id='+id
			})
		},
		// 获取收货地址列表
		async getAddressList(){
			if(!this.hasFlag) return //没有更多了
			this.page = ++this.page;
			let res = await this.$fetch(this.$api.getAddressList,{pageNum:this.page,pageSize:10},'GET')
			console.log(res);
			this.addressList = [...this.addressList,...res.rows]
			this.hasFlag  = 10*this.page < res.total
		},
		chooseAddress(item){
			if(this.flag == 1){
				uni.setStorageSync('chooseAddress',item)
				uni.navigateBack({
					delta:1
				})
			}
		}
	},
	components:{
		uniLoadMore
	},
	onLoad(options) {
		if(options.flag){
			this.flag = options.flag
		}
	},
	onShow() {
		this.addressList = []
		this.hasFlag = true
		this.getAddressList()
		// this.hasFlag = false
	},
	onNavigationBarButtonTap(){
		uni.navigateTo({
			url:'editAddress'
		})
	},
	onReachBottom() {
		this.getAddressList()
	}
}
